import React, { useState, useEffect } from 'react';
import GoogleMapReact from 'google-map-react';
import LocationMarker from './LocationMarker'
import LocationInfoBox from './LocationInfoBox'
import Loader from './Loader'

function Map() {
    const [isShown, setIsShown] = useState(false);

    const [eventData, setEventData] = useState([])
    const [loading, setLoading] = useState([])

    const [locationInfo, setLocationInfo] = useState(null) // Location (includes lat, lng)
    // This checks to see if its a wildfire
    const markers = eventData.map(ev => {
            return <LocationMarker type="wildfire" lat={ev.geometries[0].coordinates[1]} lng={ev.geometries[0].coordinates[0]} onClick={() => setLocationInfo({ id: ev.id, title: ev.title, lat: ev.geometries[0].coordinates[1], lng: ev.geometries[0].coordinates[0] })}/>
    })

    useEffect(() => {
      const fetchEvents = async () => {
        setLoading(true);
        const res = await fetch('https://eonet.gsfc.nasa.gov/api/v3/events/geojson?category=wildfires')
        const { events } = await res.json()
  
        setEventData(events)
        setLoading(false)
      }
  
      fetchEvents()
    }, [])
  
    const handleSubmit = (event) => {
        event.preventDefault();
        localStorage.setItem("coordinates", locationInfo)
        window.location.href = '/mint'; // Redirect to NFT minting page 
    }

    const defaultProps = {
        center: {
          lat: 10.99835602,
          lng: 77.01502627
        },
        zoom: 11
      };
    
    let key;
    fetch('/get_googleMaps', { // get google maps api key        
        method: "post",
        headers: { "Content-Type": "application/json" },
    }).then(function (response) {
        return response.json()
    }).then(function (data) {
        key = data;
    }).catch(function (error) {
        console.error(error);
    });
    return (
    <div>
        { !loading ? <Map eventData={eventData}/> : <Loader /> }
        <GoogleMapReact
            onClick={ev => {
                setLocationInfo({ id: "N/A", title: "N/A", lat: ev.latLng.lat(), lng: ev.latLng.lng() })
                setIsShown(current => !current);
            }}
            mapContainerClassName="map-container"
            bootstrapURLKeys={"AIzaSyDgWGt5XiCpv1gAwVh0jNREY8EG7u5oHBA"}
            yesIWantToUseGoogleMapApiInternals
            defaultCenter={defaultProps.center}
            defaultZoom={defaultProps.zoom}
        >
            <LocationMarker lat={defaultProps.center.lat} lng={defaultProps.center.lng}/>
            {markers}
        </GoogleMapReact>
        {locationInfo && <LocationInfoBox info={locationInfo} />}
        {isShown && (
            <button onClick={handleSubmit} className="btn btn-secondary btn-sm m-2">Proceed</button>
        )}
    </div>
    );
}

export default Map;